// ==========================================================================
// Header Height Compensation
// ==========================================================================

/**
 * –ö–æ–º–ø–µ–Ω—Å–∏—Ä—É–µ—Ç –≤—ã—Å–æ—Ç—É —Ñ–∏–∫—Å–∏—Ä–æ–≤–∞–Ω–Ω–æ–≥–æ —Ö–µ–¥–µ—Ä–∞
 * –î–æ–±–∞–≤–ª—è–µ—Ç padding-top –∫ body —Ä–∞–≤–Ω—ã–π –≤—ã—Å–æ—Ç–µ .header
 */

document.addEventListener('DOMContentLoaded', function () {
  function updateBodyPadding() {
    const header = document.querySelector('.header');
    if (header) {
      const headerHeight = Math.round(header.offsetHeight);
      if (headerHeight > 0) {
        document.body.style.paddingTop = headerHeight + 'px';
      }
    }
  }

  // –ü—Ä–∏–º–µ–Ω—è–µ–º –ø—Ä–∏ –∑–∞–≥—Ä—É–∑–∫–µ
  updateBodyPadding();

  // Debounce –¥–ª—è resize
  let resizeTimeout;
  window.addEventListener('resize', function () {
    clearTimeout(resizeTimeout);
    resizeTimeout = setTimeout(updateBodyPadding, 100);
  });

  // –û–±—Ä–∞–±–æ—Ç–∫–∞ –∏–∑–º–µ–Ω–µ–Ω–∏—è –æ—Ä–∏–µ–Ω—Ç–∞—Ü–∏–∏
  window.addEventListener('orientationchange', function () {
    setTimeout(updateBodyPadding, 200);
  });
});

// ==========================================================================
// Mobile Menu Functionality
// ==========================================================================

/**
 * –£–ø—Ä–∞–≤–ª–µ–Ω–∏–µ –º–æ–±–∏–ª—å–Ω—ã–º –º–µ–Ω—é
 */
document.addEventListener('DOMContentLoaded', function () {
  const header = document.querySelector('.header');
  const mobileMenuToggle = document.querySelector('.mobile-menu-toggle');
  const mobileMenuOverlay = document.querySelector('.mobile-menu-overlay');
  const navLinks = document.querySelectorAll('.nav-link');

  if (!mobileMenuToggle || !header) return;

  // –û—Ç–∫—Ä—ã—Ç–∏–µ/–∑–∞–∫—Ä—ã—Ç–∏–µ –º–æ–±–∏–ª—å–Ω–æ–≥–æ –º–µ–Ω—é
  function toggleMobileMenu() {
    const isOpen = header.classList.contains('mobile-menu-open');

    if (isOpen) {
      closeMobileMenu();
    } else {
      openMobileMenu();
    }
  }

  // –û—Ç–∫—Ä—ã—Ç–∏–µ –º–æ–±–∏–ª—å–Ω–æ–≥–æ –º–µ–Ω—é
  function openMobileMenu() {
    header.classList.add('mobile-menu-open');
    document.body.style.overflow = 'hidden';
    mobileMenuToggle.setAttribute('aria-expanded', 'true');
    mobileMenuToggle.setAttribute('aria-label', '–ó–∞–∫—Ä—ã—Ç—å –º–µ–Ω—é');

    // –§–æ–∫—É—Å –Ω–∞ –ø–µ—Ä–≤—É—é —Å—Å—ã–ª–∫—É –≤ –º–µ–Ω—é
    const firstNavLink = document.querySelector('.nav-link');
    if (firstNavLink) {
      setTimeout(() => firstNavLink.focus(), 300);
    }
  }

  // –ó–∞–∫—Ä—ã—Ç–∏–µ –º–æ–±–∏–ª—å–Ω–æ–≥–æ –º–µ–Ω—é
  function closeMobileMenu() {
    header.classList.remove('mobile-menu-open');
    document.body.style.overflow = '';
    mobileMenuToggle.setAttribute('aria-expanded', 'false');
    mobileMenuToggle.setAttribute('aria-label', '–û—Ç–∫—Ä—ã—Ç—å –º–µ–Ω—é');

    // –í–æ–∑–≤—Ä–∞—â–∞–µ–º —Ñ–æ–∫—É—Å –Ω–∞ –∫–Ω–æ–ø–∫—É –º–µ–Ω—é
    mobileMenuToggle.focus();
  }

  // –û–±—Ä–∞–±–æ—Ç—á–∏–∫ –∫–ª–∏–∫–∞ –ø–æ –∫–Ω–æ–ø–∫–µ –≥–∞–º–±—É—Ä–≥–µ—Ä–∞
  mobileMenuToggle.addEventListener('click', function (e) {
    e.preventDefault();
    e.stopPropagation();
    toggleMobileMenu();
  });

  // –û–±—Ä–∞–±–æ—Ç—á–∏–∫ –∫–ª–∏–∫–∞ –ø–æ –æ–≤–µ—Ä–ª–µ—é
  if (mobileMenuOverlay) {
    mobileMenuOverlay.addEventListener('click', function () {
      closeMobileMenu();
    });
  }

  // –ó–∞–∫—Ä—ã—Ç–∏–µ –º–µ–Ω—é –ø—Ä–∏ –∫–ª–∏–∫–µ –Ω–∞ —Å—Å—ã–ª–∫–∏ –Ω–∞–≤–∏–≥–∞—Ü–∏–∏
  navLinks.forEach(link => {
    link.addEventListener('click', function () {
      // –ù–µ–±–æ–ª—å—à–∞—è –∑–∞–¥–µ—Ä–∂–∫–∞ –¥–ª—è –ø–ª–∞–≤–Ω–æ—Å—Ç–∏
      setTimeout(() => {
        closeMobileMenu();
      }, 150);
    });
  });

  // –ó–∞–∫—Ä—ã—Ç–∏–µ –º–µ–Ω—é –ø—Ä–∏ –Ω–∞–∂–∞—Ç–∏–∏ Escape
  document.addEventListener('keydown', function (e) {
    if (e.key === 'Escape' && header.classList.contains('mobile-menu-open')) {
      closeMobileMenu();
    }
  });

  // –ó–∞–∫—Ä—ã—Ç–∏–µ –º–µ–Ω—é –ø—Ä–∏ –∏–∑–º–µ–Ω–µ–Ω–∏–∏ —Ä–∞–∑–º–µ—Ä–∞ —ç–∫—Ä–∞–Ω–∞
  window.addEventListener('resize', function () {
    if (window.innerWidth >= 768) {
      // tablet breakpoint
      closeMobileMenu();
    }
  });

  // –û–±—Ä–∞–±–æ—Ç–∫–∞ —Ñ–æ–∫—É—Å–∞ –¥–ª—è –¥–æ—Å—Ç—É–ø–Ω–æ—Å—Ç–∏
  function trapFocus(container) {
    const focusableElements = container.querySelectorAll(
      'a[href], button, textarea, input[type="text"], input[type="radio"], input[type="checkbox"], select'
    );
    const firstFocusableElement = focusableElements[0];
    const lastFocusableElement =
      focusableElements[focusableElements.length - 1];

    container.addEventListener('keydown', function (e) {
      if (e.key === 'Tab') {
        if (e.shiftKey) {
          if (document.activeElement === firstFocusableElement) {
            lastFocusableElement.focus();
            e.preventDefault();
          }
        } else {
          if (document.activeElement === lastFocusableElement) {
            firstFocusableElement.focus();
            e.preventDefault();
          }
        }
      }
    });
  }

  // –ü—Ä–∏–º–µ–Ω—è–µ–º trap focus –∫ –º–æ–±–∏–ª—å–Ω–æ–º—É –º–µ–Ω—é
  const navWrapper = document.querySelector('.nav-and-actions-wrapper');
  if (navWrapper) {
    trapFocus(navWrapper);
  }
});
